.downloadPdf {
  &__title {
    font-family: "ibmplexsansbold";
    font-size: 16px;
    color: #272A41;
    line-height: 16px;
    margin-bottom: 8px;
  }
  &__subtitle {
    font-family: "ibmplexsans";
    font-size: 14px;
    margin-bottom: 16px;
    word-wrap: break-word;
  }
  &__button {
    background: #0093FF;
    border: none;
    width: 100%;
    color: white;
    height: 40px;
    font-family: "ibmplexsans";
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    &:disabled {
      cursor: auto;
    }
  }
  &__button_state {
    margin-right: 10px;
  }
  &__loader {
    position: relative;
  }
  &__loader_wrapper {
    margin-bottom: 7px;
    margin-left: 6px;
    margin-right: 4px;
  }
  &__small_button {
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    background: #0093FF;
    color: white;
    border: none;
    height: 40px;
    font-family: "ibmplexsans";
    width: 100%;
    border-radius: 3px;
  }
  &__small_button_link {
    text-decoration: none;
    width: 48%;
    border-radius: 3px;
  }
  &__small_blue_button_link {
    background: #0093FF;
  }
  &__small_black_button_link {
    background-color: #000000;
  }
  &__button_icon {
    margin-right: 10px;
  }
  &__small_black {
    background-color: #000000;
    width: 100%;
  }
  &__black_loader_wrapper {
    margin-bottom: 4px;
    margin-left: 10px;
    margin-right: -6px;
  }
  &__black_loader_text {
    font-size: 11px;
  }
  &__buttons_wrapper {
    display: flex;
    justify-content: space-between;
    gap: 10px;
  }
}
