.dotsMenuWrapper {
    width: 100%;
    height: auto;
    background: grey;
}

.dotsMenu {
    display: flex;
    cursor: pointer;
    position: relative;
    &__dot {
        width: 4px;
        height: 4px;
        margin: 2px;
        background: #707281;
        border-radius: 50%;
        display: block;
    }
    &__open {
        width: 205px;
        height: auto;
        background-color: #ffffff;
        position: absolute;
        right: 0;
        top: 13px;
        border: 1px solid #7D829C;
        box-shadow: 0px 6px 16px rgba(73, 72, 211, 0.12);
        padding: 7px 0px;
        border-radius: 3px;
    }
    &__open_item {
        display: block;
        padding: 8px 15px;
        text-decoration: auto;
        font-family: "ibmplexsans";
        font-style: normal;
        font-weight: 400;
        font-size: 15px;
        color: #1E2029;
        &:-webkit-any-link:active {
            text-decoration: none;
            color: #707281;
        } 
        &:hover {
            background: #F2F2F5;
        }
        &_link {
            color: #0093FF;
            margin: 0px 3px;
            text-decoration: none;
        }
        &_icon {
            font-family: none;
        }
    }
    &__open_lastItem {
        margin-top: 6px;
        font-size: 12px;
        margin-bottom: -7px;
        &:hover {
            background: none;
            cursor: auto;
        }
    }
}