.form {
  &__input {
    border: 1px solid #9b9ca7;
    color: #000000;
    border-radius: 3px;
    height: 40px;
    width: 100%;
    margin-bottom: 8px;
    padding-left: 38px;
    box-sizing: border-box;
  }

  &__input::placeholder {
    font-family: "ibmplexsans";
  }
  &__input:-webkit-autofill {
    background-color: unset;
  }
  &__input:-webkit-autofill {
    background-color: unset;
  }
  &__input:focus-visible {
    outline: #0093ff auto 1px;
  }

  &__letter {
    background: url(../../assets/icons/letter.svg) no-repeat scroll 10px 9px;
  }

  &__lock {
    background: url(../../assets/icons/lock.svg) no-repeat 10px 10px;
  }

  &__lock_error {
    outline: #ff0000 auto 1px;
    &:focus-visible {
      outline: #ff0000 auto 1px;
    }
  }

  &__input_email_error, &__input_password_error  {
    display: block;
    color: #ff0000;
    font-size: 14px;
  }

  &__input_email_error {
    margin-top: -19px;
  }

  &__input_password_error {
    margin-top: -9px;
    margin-bottom: -10px;
  }

  &__input_empty {
    display: none;
  }

  &__button {
    background: #0093ff;
    border: none;
    color: white;
    width: 100%;
    height: 40px;
    font-family: "ibmplexsans";
    font-size: 15px;
    margin-top: 8px;
    margin-bottom: 16px;
    cursor: pointer;
    border-radius: 3px;
    font-weight: 600;
    &:disabled {
      cursor: auto;
      opacity: .5;
    }
  }

  &__link_wrapper {
    display: flex;
    justify-content: right;
  }

  &__link {
    display: block;
    text-decoration: none;
    color: #0093ff;
    font-size: 12.5px;
    margin-bottom: 10px;
    margin-top: 10px;
  }

  &__footer {
    font-family: "ibmplexsans";
    font-size: 12.5px;
    text-align: center;
    color: #707281;

    & a {
      text-decoration: none;
      margin-left: 5px;
      color: #0093FF;
    }
  }
  &__nopdf {
    font-family: "ibmplexsans";
    font-size: 19px;
  }
}