.login_header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  &__log_out {
    font-family: 'ibmplexsans';
    font-weight: 400;
    font-size: 15px;
    line-height: 20px;
    color: #707281;
    cursor: pointer;
  }
  &__error {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: -20px -25px 16px -20px;
    height: 32px;
    background: #D9302A;    
    color: #ffffff;
    font-family: "ibmplexsans";
    & a {
      color: #ffffff;
    }
  }
}
