ul {
  padding-inline-start: 20px;
  margin-block-end: unset;
  margin-block-start: unset;
}

li {
  list-style-type: none;
  position: absolute;
  top: 0px;
  left: 0px;
  width: 3px;
  height: 3px;
  background: #FFFFFF;
  border-radius: 50%;
}

.a {
  animation: a 1s ease-in-out infinite;
  top: -5px;
  left: -5px;
}

.b {
  animation: b 1s ease-in-out infinite;
  top: -5px;
  left: 0px;
}

.c {
  animation: c 1s ease-in-out infinite;
  top: -5px;
  left: 5px;
}

.d {
  animation: d 1s ease-in-out infinite;
  top: 0px;
  left: -5px;
}

.f {
  animation: f 1s ease-in-out infinite;
  top: 0px;
  left: 5px;
}

.g {
  animation: g 1s ease-in-out infinite;
  top: 5px;
  left: -5px;
}

.h {
  animation: h 1s ease-in-out infinite;
  top: 5px;
  left: 2px;
}

.i {
  animation: i 1s ease-in-out infinite;
  top: 5px;
  left: 5px;
}

@keyframes a {
  50% {
    top: 0px;
    left: -5px;
  }
  100% {
    top: 0px;
    left: -5px;
  }
}

@keyframes b {
  50% {
    top: -5px;
    left: -5px;
  }
  100% {
    top: -5px;
    left: -5px;
  }
}

@keyframes c {
  50% {
    top: -5px;
    left: 0px;
  }
  100% {
    top: -5px;
    left: 0px;
  }
}

@keyframes d {
  50% {
    top: 5px;
    left: -5px;
  }
  100% {
    top: 5px;
    left: -5px;
  }
}

@keyframes f {
  50% {
    top: -5px;
    left: 5px;
  }
  100% {
    top: -5px;
    left: 5px;
  }
}

@keyframes g {
  50% {
    top: 5px;
    left: 0px;
  }
  100% {
    top: 5px;
    left: 0px;
  }
}

@keyframes h {
  50% {
    top: 5px;
    left: 5px;
  }
  100% {
    top: 5px;
    left: 5px;
  }
}

@keyframes i {
  50% {
    top: 0px;
    left: 5px;
  }
  100% {
    top: 0px;
    left: 5px;
  }
}
