.app {
  width: 325px;
  margin: 20px;
  padding-right: 5px;
  border-radius: none;
  padding-bottom: 40px;
  &__header {
    display: flex;
    justify-content: center;
    margin-bottom: 24px;
    color: #000000;
    font-weight: bold;
  }
  &__img {
    width: 32px;
    margin-right: 8px;
  }
  &__title {
    font-size: 25px;
    font-family: "ibmplexsansbold";
  }
}
